<template>
  <layout-default>
    <div class="page-content">
      <div class="container-fluid">

          <!-- start page title -->
          <div class="row">
              <div class="col-12">
                  <div class="page-title-box d-flex align-items-center justify-content-between">
                      <h4 class="mb-0">معلومات العميل</h4>
                      <div class="page-title-right">
                          <form-modal ref="FormModal" :fetchData="fetchData"/>
                      </div>

                  </div>
              </div>
          </div>
          <!-- end page title -->
          
          <div class="row mb-4" >
            <div class="col-12" v-if="client">
                <div class="card h-100">
                    <div class="card-body">
                        <div class="text-center">
                            <div class="dropdown float-end">
                                <a class="text-body dropdown-toggle font-size-18" href="#" role="button" data-bs-toggle="dropdown" aria-haspopup="true">
                                    <i class="uil uil-ellipsis-v"></i>
                                </a>
                                
                                <div class="dropdown-menu dropdown-menu-end">
                                    <a @click="$refs.FormModal.edit(client)" class="dropdown-item" href="javascript:;">
                                      <i class="far fa-edit"></i> تعديل البيانات
                                    </a>
                                    <a @click="$refs.FormModal.destroy(client.id)" class="dropdown-item" href="javascript:;">
                                      <i class="far fa-trash-alt"></i> حذف العميل
                                    </a>
                                </div>
                            </div>
                            <div class="clearfix"></div>
                        </div>

                        <div class="text-muted">
                            <div class="mt-4">
                                <div class="row ">
                                    <p class="mt-4 mb-1 col-md-3 font-size-15"><b style="float: left;"> الأسم : </b>	&nbsp; {{client.name}} </p>
                                    <p class="mt-4 mb-1 col-md-3 font-size-15" >
                                      <b style="float: left;"> تاريخ التسجيل : </b>
                                    	&nbsp; {{client.created_at| moment("DD-MM-YYYY")}}
                                    </p>
                                    <hr>
                                    <p class="mt-4 mb-1 col-md-4 font-size-15" >
                                      <b style="float: left;"> أستعلام جنائي : </b>
                                    	&nbsp; 
                                      <span v-if="client.request_1" class="text-success">
                                        <i class="far fa-check-circle"></i> تم الأستعلام
                                      </span>
                                      <span v-else class="text-danger">
                                        <i class="far fa-times-circle"></i> لم يتم الأستعلام
                                      </span>
                                    </p>
                                    <p class="mt-4 mb-1 col-md-4 font-size-15" >
                                      <b style="float: left;"> أستعلام ميداني : </b>
                                    	&nbsp; 
                                      <span v-if="client.request_2" class="text-success">
                                        <i class="far fa-check-circle"></i> تم الأستعلام
                                      </span>
                                      <span v-else class="text-danger">
                                        <i class="far fa-times-circle"></i> لم يتم الأستعلام
                                      </span>
                                    </p>
                                    <p class="mt-4 mb-1 col-md-4 font-size-15" v-for="(info, index) in client.info">
                                      <b style="float: left;"> {{info.name}} : </b>	&nbsp; {{info.pivot.data}} 
                                    </p>
                                </div>
                            </div>
                        </div>
                        <h5 class="font-size-20 mb-4 mt-5 text-center" style="font-weight:600;">فواتير العميل</h5>
                        <table id="datatable" class="table table-bordered dt-responsive nowrap" style="border-collapse: collapse; border-spacing: 0; width: 100%;">
                          <thead>
                            <tr>
                                <th>رقم الفاتورة</th>
                                <th>إجمالي الفاتورة</th>
                                <th>مقدم</th>
                                <th>شهور التقسيط</th>
                                <th>تاريخ الفاتورة</th>
                                <th>تاريخ التسليم</th>
                                <th>خيارات</th>
                            </tr>
                          </thead>
                          <tbody>
                            <tr v-for="(order,index) in client.orders">
                              <td>#{{order.id}}{{order.created_at| moment("DDMMYY")}}</td>
                              <td>{{moneyFormat(order.total,0)}} L.E</td>
                              <td>{{moneyFormat(order.pay,0)}} L.E</td>
                              <td v-if="order.status<2">
                                <span :class="(order.payment_count==order.months)?'text-success':'text-danger'">
                                {{order.payment_count}}</span>/{{order.months}}
                              </td> 
                              <td v-else>
                                <span class="text-success">{{order.months}}</span>/{{order.months}}
                              </td>
                              <td>{{order.created_at| moment("DD-MM-YYYY")}}</td>
                              <td>{{order.confirmed_at| moment("DD-MM-YYYY")}}</td>
                              <td>
                                <div class="btn-group">
                                    <button type="button" class="btn btn-light dropdown-toggle" data-bs-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                                      المزيد <i class="mdi mdi-chevron-down"></i>
                                    </button>
                                    <div class="dropdown-menu" style="min-width: 120px;">
                                        <router-link :to="{ name: 'client_orders_show', params: { order_id: order.id } }" tag="a" class="dropdown-item">
                                            <i class="far fa-eye"></i> رؤية
                                        </router-link>
                                        <a @click="$refs.FormModal.destroyOrder(order.id)" class="dropdown-item dropdownItem" href="javascript:;">
                                          <i class="far fa-trash-alt"></i> حذف
                                        </a>
                                    </div>
                                </div>
                              </td>
                            </tr>
                          </tbody>
                        </table>
                    </div>
                </div>
                
            </div>
        </div>
      </div> <!-- container-fluid -->
    </div>
  </layout-default>
</template>  


<script>
import LayoutDefault from '../../layouts/App';
import PaginationNav from '../../components/pagination';
import FormModal from '../../components/modals/client/add';
import axios from 'axios';

export default {
  components: {LayoutDefault,PaginationNav,FormModal},
  data() {
      return {client:null,loader:false}
    },  
  created() {
    this.fetchData();
  },
  methods: {
    fetchData() {
      this.loader=true;
      axios.get('/api/clients/'+this.$route.params.client_id).then(({data}) => {
          this.client=data.success.client;
          this.loader=false;
      }).catch((error) => {this.$router.push({ name: "clients"})});  
    },
  },
}
</script>